import { setDB, getDB } from '@/utils/db'

export default {
  namespaced: true,
  actions: {
    set (context, { path = '', value = '' }) {
      return new Promise((resolve) => {
        setDB(path, value)
        resolve()
      })
    },
    /**
     * @description 
     * @description  dbName.path || defaultValue
     * @param {Object} context
     * @param {Object} payload dbName {String} 
     * @param {Object} payload path {String} 
     * @param {Object} payload defaultValue {*} 
     * @param {Object} payload user {Boolean} 
     */
    get (context, { path }) {
      return new Promise((resolve) => {
        const info = getDB(path)
        resolve(info)
      })
    }
  }
}
